@font-face {
    font-family: "SSF4 ABUKET";
    src: local("SSF4 ABUKET");
}

ul{
    margin-left: 25px;
    padding-left: 0;
}

body {
    background-color: #D0E0FE;
}

.Controls_Header_Text {
    font-family: "SSF4 ABUKET";
    font-size: 28px;
    font-weight: bold;
    text-align: center;
    background: -webkit-linear-gradient(#FFED01, #FF520B);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 1px black;

}

.Controls_Text {
    font-family: "SSF4 ABUKET";
    font-size: 16px;
    text-align: left;
    background: -webkit-linear-gradient(#FFED01, #FF520B);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 1px black;
    list-style-type:none;
}

.Controls_Canvas {
    background-image: url("images/ControlsPanel.png");
    background-size: contain;
    width: 255px;
    height: 510px;
    float: left;
    margin-left: 20px;
    padding-top: 15px;
}

.Canvas_Panel {
    background-image: url("images/CanvasPanel.png");
    background-size: contain;
    width: 945px;
    height: 600px;
    float: right;
    margin-right: 250px;
}

.Game_Canvas {
    width: 875px;
    height: 522px;
    position: absolute;
    margin-left: 35px;
    margin-top: 39px;
}
